.popular {
  padding: 70px 0;
  position: relative;
}
.popular .container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.popular .container .info {
  text-align: center;
}
.popular .container .info h3 {
  color: var(--title-color);
  font-size: 38px;
  font-family: var(--second-font);
  letter-spacing: 2px;
  line-height: 1.3;
}
.popular .container .info p {
  width: 40%;
  color: var(--text-color);
  margin: 30px auto 60px;
  line-height: 1.8;
}
@media (max-width: 991px) {
  .popular .container .info p {
    width: 60%;
  }
}
@media (max-width: 767px) {
  .popular .container .info p {
    width: 80%;
  }
}
.popular .container .pizza-container {
  position: relative;
  width: 300px;
  height: 300px;
  margin: auto;
}

.popular .container .pizza-container .dish {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.popular .container .pizza-container .dish-image {
  width: 100%;
  height: auto;
}

.popular .container .pizza-container .pizza {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.popular .container .pizza-container .pizza img {
  width: 80%;
  height: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.3s;
}
.popular .container .pizza-container i.left {
  color: var(--first-color-dark);
  position: absolute;
  top: 50%;
  left: -90px;
  cursor: pointer;
}
.popular .container .pizza-container i.right {
  color: var(--first-color-dark);
  position: absolute;
  top: 50%;
  right: -90px;
  cursor: pointer;
}
@media (max-width: 991px) {
  .popular .container .pizza-container i.left,
  .popular .container .pizza-container i.right {
    display: none;
  }
}
.hidden {
  display: none;
}
