.contact {
  padding: 70px 0;
}
.contact .container {
  padding: 70px 60px 0;
  background-color: var(--title-color);
  border-radius: 85px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.contact .container .image,
.contact .container .info {
  flex-basis: 45%;
}
.contact .container .info {
  position: relative;
}
@media (max-width: 991px) {
  .contact .container .image {
    display: none;
  }
}
@media (max-width: 991px) {
  .contact .container .info {
    flex-basis: 100%;
  }
}
.contact .container h3 {
  margin-bottom: 50px;
  color: var(--container-color);
  font-family: var(--second-font);
  letter-spacing: 2px;
  font-size: 35px;
}
@media (max-width: 991px) {
  .contact .container h3 {
    text-align: center;
  }
}
.contact .container .info .details {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 991px) {
  .contact .container .info .details {
    flex-direction: column;
    text-align: center;
  }
}
.contact .container .info .details .box {
  flex-basis: 50%;
  margin-bottom: 30px;
}
@media (max-width: 991px) {
  .contact .container .info .details .box {
    flex-basis: 100%;
    width: 100%;
  }
}
.contact .container .info .details .box p {
  margin-bottom: 15px;
  color: var(--container-color);
  font-family: var(--second-font);
  letter-spacing: 2px;
}
.contact .container .info .details .box .icons a {
  margin-right: 5px;
  color: var(--first-color);
}
.contact .container .info .details .box .icons a i {
  font-size: 15px;
}
.contact .container .info .details .box span {
  color: var(--white-color);
  font-size: 11px;
  letter-spacing: 2px;
}
.contact .container .info .details .box a {
  display: inline-block;
  margin-top: 10px;
  color: var(--first-color);
  font-size: 12px;
  letter-spacing: 1px;
}
.contact .container .info .details .box a i {
  margin-right: 5px;
  font-size: 10px;
}
