.recipe {
  padding: 70px 0;
}
.recipe .container {
  margin-top: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.recipe > h3 {
  color: var(--title-color);
  font-size: 30px;
  font-family: var(--second-font);
  letter-spacing: 2px;
  line-height: 1.3;
  text-align: center;
}
.recipe .container .cards,
.recipe .container .image {
  flex-basis: 45%;
}
@media (max-width: 991px) {
  .recipe .container .image {
    display: none;
  }
}
@media (max-width: 991px) {
  .recipe .container .cards {
    flex-basis: 100%;
  }
}
.recipe .container .cards .box {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
}
@media (max-width: 991px) {
  .recipe .container .cards .box {
    margin-bottom: 45px;
    flex-direction: column;
    text-align: center;
  }
}
.recipe .container .cards .box img {
  width: 70px;
  margin-right: 25px;
}
@media (max-width: 991px) {
  .recipe .container .cards .box img {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.recipe .container .cards .box .text h3 {
  color: var(--title-color);
  font-family: var(--second-font);
  margin-bottom: 15px;
}
.recipe .container .cards .box .text p {
  color: var(--text-color);
  font-size: 13px;
}
