footer {
  padding: 70px 0 10px;
}
footer .container {
  margin-top: 50px;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  row-gap: 50px;
}
footer h3 {
  color: var(--first-color);
  font-family: var(--second-font);
  font-size: 25px;
  letter-spacing: 2px;
  text-align: center;
}
footer .container .box p {
  margin-bottom: 15px;
  color: var(--title-color);
  font-size: 20px;
  font-family: var(--second-font);
  letter-spacing: 1px;
}
footer .container .box .icons a {
  padding: 0 10px;
}
footer .container .box .icons a i {
  color: var(--first-color);
  font-size: 17px;
}
footer .container .box form {
  position: relative;
  max-width: 95%;
}
footer .container .box form input[type="email"] {
  border: none;
  outline: none;
  margin: 0 auto;
  width: calc(100% - 10%);
  height: 50px;
  border-radius: 25px;
  padding-left: 15px;
  font-size: 12px;
}
footer .container .box form input[type="email"]::placeholder {
  transition: 0.3s;
}
footer .container .box form input[type="email"]:focus::placeholder {
  opacity: 0;
}
footer .container .box form input[type="submit"] {
  width: 120px;
  height: 40px;
  color: var(--white-color);
  background-color: var(--first-color);
  font-size: 12px;
  letter-spacing: 1px;
  border: none;
  outline: none;
  border-radius: 25px;
  cursor: pointer;
  position: absolute;
  right: 25px;
  bottom: 5px;
}
footer .container .box img {
  width: 60px;
  padding: 0 10px;
}
footer a.terms {
  margin-top: 45px;
  color: var(--text-color);
  display: block;
  text-align: center;
  font-size: 12px;
}
footer p {
  margin-bottom: 0;
  margin-top: 25px;
  color: var(--text-color);
  text-align: center;
  font-size: 12px;
}
