.products {
  padding: 70px 0;
}
.products .container h3 {
  color: var(--title-color);
  margin-bottom: 110px;
  font-size: 38px;
  font-family: var(--second-font);
  letter-spacing: 2px;
  line-height: 1.3;
  text-align: center;
}
.products .container .cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  row-gap: 90px;
}
.products .container .cards .box {
  width: 250px;
  height: 220px;
  margin: 0 auto;
  background-color: var(--container-color);
  border-radius: 15px;
  box-shadow: 0 8px 24px hsla(353, 100%, 8%, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.products .container .cards .box img {
  width: 140px;
  position: absolute;
  top: -50%;
  transform: translateY(50%);
}
.products .container .cards p {
  color: var(--title-color);
  font-family: var(--second-font);
  font-weight: 800;
  position: absolute;
  left: 15px;
  top: 110px;
  line-height: 1.8;
}
.products .container .cards .info {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 15px;
  display: flex;
  justify-content: space-between;
  color: var(--first-color);
  font-family: var(--second-font);
  font-weight: 800;
  font-size: 20px;
}
.products .container .cards .info span {
  padding-left: 15px;
}
.products .container .cards .info i {
  padding-right: 15px;
  cursor: pointer;
}
