.landing .container {
  height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.landing .container .info,
.landing .container .image {
  flex-basis: 45%;
  position: relative;
}
@media (max-width: 991px) {
  .landing .container .info {
    flex-basis: 100%;
  }
}
.landing .container .info h1 {
  margin: 0;
  color: var(--title-color);
  font-size: 50px;
  font-family: var(--second-font);
  letter-spacing: 3px;
  line-height: 1.3;
}
.landing .container .info p {
  margin-top: 15px;
  margin-bottom: 35px;
  color: var(--text-color);
  font-size: 13px;
  line-height: 1.8;
}
.landing .container .info a {
  display: inline-block;
  padding: 15px;
  color: var(--white-color);
  background-color: var(--first-color);
  box-shadow: 0 8px 24px hsla(353, 100%, 40%, 0.4);
  letter-spacing: 1px;
}
.landing .container .info .sticker-one {
  position: absolute;
  right: 149px;
  top: 23px;
  width: 35px;
}
@media (min-width: 992px) and (max-width: 1199px) {
  .landing .container .info .sticker-one {
    right: 60px;
  }
}
@media (max-width: 991px) {
  .landing .container .info .sticker-one {
    top: -50px;
    right: 30px;
  }
}
.landing .container .info .sticker-two {
  position: absolute;
  left: 195px;
  bottom: 45px;
  width: 25px;
}

@media (max-width: 991px) {
  .landing .container .image {
    display: none;
  }
}
.landing .container .image img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.landing .container .image .board-image {
  z-index: 1;
}
.landing .container .image .pizza-image {
  z-index: 2;
}
.landing .container .image .leaf-one-image {
  width: 70px;
  z-index: 3;
  top: -238px;
  left: 250px;
  transform: translate(0, 30%);
}
.landing .container .image .leaf-two-image {
  width: 70px;
  z-index: 3;
  bottom: -40px;
  left: -45px;
  transform: translate(148px, 150%);
}
.landing .container .image .pepperoni-image {
  width: 70px;
  z-index: 3;
  top: 45px;
  right: -18px;
  transform: translate(117px, -240%);
}
.landing .container .image .mushroom-image {
  width: 70px;
  z-index: 3;
  top: 0;
  right: 0;
  transform: translate(190%, 66%);
}
.landing .container .image .olive-image {
  width: 70px;
  z-index: 3;
  top: 0;
  right: 0;
  transform: translate(-11%, 200%);
}
.landing .container .image .tomato-image {
  width: 70px;
  z-index: 3;
  top: 58px;
  left: 0;
  transform: translate(0, -240%);
}
