.about-us {
  padding: 70px 0;
}
.about-us .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.about-us .container .image,
.about-us .container .info {
  flex-basis: 45%;
}
@media (max-width: 991px) {
  .about-us .container .image {
    display: none;
  }
}
.about-us .container .info {
  position: relative;
}
@media (max-width: 991px) {
  .about-us .container .info {
    flex-basis: 100%;
  }
}
.about-us .container .info h3 {
  color: var(--title-color);
  font-size: 38px;
  font-family: var(--second-font);
  letter-spacing: 2px;
  line-height: 1.3;
}
.about-us .container .info p {
  color: var(--text-color);
  margin-top: 30px;
  margin-bottom: 60px;
  line-height: 1.8;
}
.about-us .container .info .sticker {
  width: 50px;
  position: absolute;
  right: 115px;
  bottom: 50px;
}
.about-us .container .info a {
  display: inline-block;
  padding: 15px;
  color: var(--white-color);
  background-color: var(--first-color);
  box-shadow: 0 8px 24px hsla(353, 100%, 40%, 0.4);
  letter-spacing: 1px;
  border-radius: 15px;
}
