header {
  height: 70px;
  font-family: var(--second-font);
}
header .container {
  height: 70px;
  display: flex;
  align-items: center;
  position: relative;
}
header .container a {
  display: flex;
  align-items: center;
}
header .container a img {
  width: 30px;
}
header .container a span {
  color: var(--title-color);
  margin-left: 10px;
}
header .container .main-nav {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 767px) {
  header .container .main-nav {
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
    position: absolute;
    top: 100%;
    right: 0;
    width: 50%;
    height: 600px;
    background-color: var(--first-color-dark);
    display: none;
    z-index: 100;
  }
}
header .container .main-nav li {
  padding: 5px 25px;
  position: relative;
}
@media (max-width: 767px) {
  header .container .main-nav li {
    padding: 15px 25px;
  }
}
@media (max-width: 767px) {
  header .container .main-nav li {
    margin: 10px 0;
  }
}
header .container .main-nav li a {
  color: var(--title-color);
  font-size: 12px;
  position: relative;
}
header .container .main-nav li a.active::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 25px;
  height: 2px;
  background-color: var(--first-color);
}
@media (max-width: 767px) {
  header .container .main-nav li a {
    color: white;
  }
}
@media (max-width: 767px) {
  header .container .icons {
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }
}
header .container .icons i.xmark,
header .container .icons i.bars {
  display: none;
  font-size: 16px;
}
@media (max-width: 767px) {
  header .container .icons i.bars {
    display: flex;
  }
}
